"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { ChevronLeft, Check } from "lucide-react";
import ScrollWheelPicker from "@/components/scrollWhellPicker/ScrollWhellPicker";
import ScrollWheelDatePicker from "@/components/scrollWhellPicker/ScrollWheelDatePicker";

type QuizData = {
  objetivo: string;
  nivel_treino: string;
  frequencia_treino: string;
  peso_atual: string;
  altura: string;
  peso_desejado: string;
  dia_nascimento: string;
  mes_nascimento: string;
  ano_nascimento: string;
  horario_treino: string;
  alimentacao_atual: string;
  refeicoes_dia: string;
  rotina_trabalho: string[];
  nivel_disciplina: string;
  dificuldade_principal: string[];
  horario_dificil: string;
  agua: string;
  sono: string;
  tentou_dieta: string;
  expectativa: string;
  prazo_resultado: string;
  alergias: string[];
  outras_alergias: string;
};

export default function QuizPage() {
  const router = useRouter();
  const totalSteps = 22;

  const [step, setStep] = useState(1);

  const [quizData, setQuizData] = useState<QuizData>({
    objetivo: "",
    nivel_treino: "",
    frequencia_treino: "",
    peso_atual: "",
    altura: "",
    peso_desejado: "",
    dia_nascimento: "",
    mes_nascimento: "",
    ano_nascimento: "",
    horario_treino: "",
    alimentacao_atual: "",
    refeicoes_dia: "",
    rotina_trabalho: [],
    nivel_disciplina: "",
    dificuldade_principal: [],
    horario_dificil: "",
    agua: "",
    sono: "",
    tentou_dieta: "",
    expectativa: "",
    prazo_resultado: "",
    alergias: [],
    outras_alergias: "",
  });

  // Estados dos pickers
  const [pesoAtual, setPesoAtual] = useState(70);
  const [altura, setAltura] = useState(170);
  const [pesoDesejado, setPesoDesejado] = useState(65);

  const [dia, setDia] = useState(1);
  const [mes, setMes] = useState("Janeiro");
  const [ano, setAno] = useState(2000);

  const gerarArrayPeso = () => Array.from({ length: 171 }, (_, i) => 30 + i);
  const gerarArrayAltura = () => Array.from({ length: 121 }, (_, i) => 100 + i);
  const dias = Array.from({ length: 31 }, (_, i) => i + 1);
  const meses = [
    "Janeiro",
    "Fevereiro",
    "Março",
    "Abril",
    "Maio",
    "Junho",
    "Julho",
    "Agosto",
    "Setembro",
    "Outubro",
    "Novembro",
    "Dezembro",
  ];
  const anos = Array.from({ length: 100 }, (_, i) => 2024 - i);

  const handleNext = () => {
    // salvar valores dos pickers
    if (step === 4) setQuizData({ ...quizData, peso_atual: `${pesoAtual}` });
    if (step === 5) setQuizData({ ...quizData, altura: `${altura}` });
    if (step === 6)
      setQuizData({ ...quizData, peso_desejado: `${pesoDesejado}` });
    if (step === 7)
      setQuizData({
        ...quizData,
        dia_nascimento: `${dia}`,
        mes_nascimento: mes,
        ano_nascimento: `${ano}`,
      });

    if (step < totalSteps) return setStep(step + 1);

    // finalizar quiz
    localStorage.setItem("quizData", JSON.stringify(quizData));
    router.push("/quiz-result");
  };

  const handleBack = () => {
    if (step > 1) setStep(step - 1);
    else router.push("/");
  };

  const progress = (step / totalSteps) * 100;

  const renderQuestion = () => {
    switch (step) {
      case 1:
        return (
          <div className="space-y-10">
            <h2 className="text-2xl font-bold text-[#0A0A0A]">
              Qual é seu objetivo principal?
            </h2>
            <p className="text-[#6F6F6F]">Escolha uma opção</p>

            <div className="space-y-3">
              {[
                "Perder peso",
                "Ganhar massa muscular",
                "Manter peso",
                "Melhorar hábitos",
              ].map((opt) => (
                <button
                  key={opt}
                  onClick={() => setQuizData({ ...quizData, objetivo: opt })}
                  className={`w-full h-14 px-6 flex items-center justify-between rounded-xl border-2 transition
              ${
                quizData.objetivo === opt
                  ? "border-[#00C974] bg-white"
                  : "border-gray-200 bg-white"
              }`}
                >
                  <span className="font-medium text-[#0A0A0A]">{opt}</span>
                </button>
              ))}
            </div>
          </div>
        );

      case 2:
        return (
          <div className="space-y-10">
            <h2 className="text-2xl font-bold text-[#0A0A0A]">
              Qual é o seu nível de treino?
            </h2>
            <p className="text-[#6F6F6F]">Escolha uma opção</p>

            <div className="space-y-3">
              {["Iniciante", "Intermediário", "Avançado"].map((opt) => (
                <button
                  key={opt}
                  onClick={() =>
                    setQuizData({ ...quizData, nivel_treino: opt })
                  }
                  className={`w-full h-14 px-6 flex items-center justify-between rounded-xl border-2 transition
              ${
                quizData.nivel_treino === opt
                  ? "border-[#00C974] bg-white"
                  : "border-gray-200 bg-white"
              }`}
                >
                  <span className="font-medium text-[#0A0A0A]">{opt}</span>
                </button>
              ))}
            </div>
          </div>
        );

      case 3:
        return (
          <div className="space-y-10">
            <h2 className="text-2xl font-bold text-[#0A0A0A]">
              Quantas vezes você treina por semana?
            </h2>
            <p className="text-[#6F6F6F]">Escolha uma opção</p>

            <div className="space-y-3">
              {[
                "Não treino",
                "1–2x por semana",
                "3–4x por semana",
                "5–6x por semana",
                "Todos os dias",
              ].map((opt) => (
                <button
                  key={opt}
                  onClick={() =>
                    setQuizData({ ...quizData, frequencia_treino: opt })
                  }
                  className={`w-full h-14 px-6 flex items-center justify-between rounded-xl border-2 transition
              ${
                quizData.frequencia_treino === opt
                  ? "border-[#00C974] bg-white"
                  : "border-gray-200 bg-white"
              }`}
                >
                  <span className="font-medium text-[#0A0A0A]">{opt}</span>
                </button>
              ))}
            </div>
          </div>
        );
      case 4:
        return (
          <div className="space-y-10">
            <h2 className="text-2xl font-bold text-[#0A0A0A]">
              Qual é o seu peso atual?
            </h2>
            <p className="text-[#6F6F6F]">Ajuste usando o seletor abaixo</p>

            <ScrollWheelPicker
              value={pesoAtual}
              onChange={(v) => setPesoAtual(Number(v))}
              options={gerarArrayPeso()}
              unit="kg"
            />

            <p className="text-center text-4xl font-semibold">{pesoAtual} kg</p>
          </div>
        );

      case 5:
        return (
          <div className="space-y-10">
            <h2 className="text-2xl font-bold text-[#0A0A0A]">
              Qual é a sua altura?
            </h2>
            <p className="text-[#6F6F6F]">Ajuste usando o seletor abaixo</p>

            <ScrollWheelPicker
              value={altura}
              onChange={(v) => setAltura(Number(v))}
              options={gerarArrayAltura()}
              unit="cm"
            />

            <p className="text-center text-4xl font-semibold">{altura} cm</p>
          </div>
        );

      case 6:
        return (
          <div className="space-y-10">
            <h2 className="text-2xl font-bold text-[#0A0A0A]">
              Qual peso você deseja atingir?
            </h2>
            <p className="text-[#6F6F6F]">Ajuste usando o seletor abaixo</p>

            <ScrollWheelPicker
              value={pesoDesejado}
              onChange={(v) => setPesoDesejado(Number(v))}
              options={gerarArrayPeso()}
              unit="kg"
            />

            <div className="text-center">
              <p className="text-4xl font-semibold">{pesoDesejado} kg</p>
              <p className="text-red-500 mt-2">
                {pesoDesejado - pesoAtual >= 0 ? "+" : ""}
                {pesoDesejado - pesoAtual} kg
              </p>
            </div>
          </div>
        );

      case 7:
        return (
          <div className="space-y-8">
            <h2 className="text-2xl font-bold text-[#0A0A0A]">
              Quando você nasceu?
            </h2>
            <p className="text-[#6F6F6F]">Selecione sua data de nascimento</p>

            <div className="flex justify-center gap-10">
              <div className="flex flex-col items-center">
                <span className="text-sm text-[#6F6F6F] mb-2">Dia</span>
                <ScrollWheelPickerDate
                  value={dia}
                  onChange={(v) => setDia(Number(v))}
                  options={dias}
                />
              </div>

              <div className="flex flex-col items-center">
                <span className="text-sm text-[#6F6F6F] mb-2">Mês</span>
                <ScrollWheelPickerDate
                  value={mes}
                  onChange={(v) => setMes(String(v))}
                  options={meses}
                />
              </div>

              <div className="flex flex-col items-center">
                <span className="text-sm text-[#6F6F6F] mb-2">Ano</span>
                <ScrollWheelPickerDate
                  value={ano}
                  onChange={(v) => setAno(Number(v))}
                  options={anos}
                />
              </div>
            </div>
          </div>
        );

      default:
        return (
          <h2 className="text-xl font-bold">
            Passo {step} não configurado ainda (mas seu quiz continua
            funcionando)
          </h2>
        );
    }
  };

  return (
    <div className="min-h-screen bg-white">
      <div className="max-w-2xl mx-auto px-6 py-8">
        {/* Header */}
        <div className="mb-8">
          <div className="flex items-center justify-between">
            <Button onClick={handleBack} variant="ghost" className="p-2">
              <ChevronLeft className="w-6 h-6" />
            </Button>
            <span className="text-sm text-gray-600">
              {step} de {totalSteps}
            </span>
          </div>

          <div className="w-full h-2 bg-gray-200 rounded-full mt-4">
            <div
              className="h-full bg-[#00C974] rounded-full transition-all"
              style={{ width: `${progress}%` }}
            />
          </div>
        </div>

        {renderQuestion()}

        <Button
          onClick={handleNext}
          className="w-full h-14 mt-10 rounded-full bg-[#00C974] hover:bg-[#00B368] text-white text-lg"
        >
          {step === totalSteps ? "Finalizar" : "Continuar"}
        </Button>
      </div>
    </div>
  );
}
